<template>
  <div class="table__wrap">
    <div class="table">
      <div class="table__head">
        <div class="table__row">
          <div
            v-for="item in columns"
            :key="item.id"
            class="table__head__column table__column"
            :style="{ '--width': (item.width ? item.width + 'px' : 'unset') }"
          >
            <slot name="column" :column="item" />
          </div>
          <div class="table__head__column table__column" />
        </div>
      </div>
      <div class="table__body">
        <template v-for="(item, i) in data" :key="item.id">
          <Container box-shadow class="table__row">
            <div
              v-for="col in columns"
              :key="col.id"
              class="table__row__cell table__column"
              :style="{
             '--width': col.width ? col.width + 'px' : 'unset',
             '--overflow-cell': col.width ? 'hidden' : 'unset'
            }"
            >
              <div class="table__row__cell-wrap">
                <template v-if="item[col.name]">
                  <template v-if="col.icon">
                    <Icon v-if="item[col.name].icon" :name="item[col.name].icon" class="table__row__cell-icon" />
                    <span v-else :style="{ marginRight: col.icon + 16 + 'px' }" />
                  </template>
                  <slot name="cell" :item="(item[col.name].cellValue || item[col.name])" :column="col" />
                  <Icon
                    v-if="col.copy"
                    class="table__row__cell-copy"
                    name="copy"
                    @click="$emit('copy-cell', { col, item })"
                  />
                </template>
              </div>
            </div>
            <div
              v-if="expand"
              class="table__row__cell table__column"
              @click="onExpand(i)"
            >
              <Icon name="arrow-down" width="9" height="7" />
            </div>
          </Container>
          <div v-if="expandIndex === i" class="table__expand-row">
            <td colspan="20">
              <slot name="expand" :item="item"/>
            </td>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import Icon from '@/components/Icon/Icon'
import { ref } from 'vue'
import Container from '@/components/Container'
export default {
  name: 'Table',
  components: { Container, Icon },
  props: {
    columns: {
      type: Array,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    },
    expand: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const expandIndex = ref(-1)

    const onExpand = (i) => {
      expandIndex.value = expandIndex.value === i ? -1 : i
    }

    return {
      expandIndex,
      onExpand
    }
  }
}
</script>

<style lang="scss">
.table {
  display: table;
  width: 100%;

  &__wrap {
    overflow: auto;
  }
  &__column {
    position: relative;
    display: table-cell;
    padding: 14px 0 14px 20px;
    text-overflow: ellipsis;
    white-space: nowrap;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

    &:first-child:before {
      position: absolute;
      content: '';
      display: block;
      height: 1px;
      width: 20px;
      background: white;
      top: 100%;
      left: 0;
    }
  }
  &__head {
    display: table-header-group;

    .table__row:hover, .table__row:hover &__cell {
      background: unset;
    }
    &__column {
      width: var(--width);
      padding-top: 8px;
      padding-bottom: 8px;
      border-bottom: 2px solid rgba(0, 0, 0, 0.1);

      color: #000000;
      opacity: 0.5;

      &:first-child:before {
        top: calc(100% - 1px);
        height: 5px;
      }
    }
  }
  &__body {
    display: table-row-group;
  }
  &__expand-row {
    display: table-row;
  }
  &__row {
    display: table-row;
    border-radius: 4px;

    &__cell {
      &-wrap {
        width: var(--width);
        white-space: nowrap;
        overflow: var(--overflow-cell);
        text-overflow: ellipsis;
      }
      &:first-child {
        border-radius: 4px 0 0 4px;
      }
      .icon {
        display: inline-block;
      }
      &-icon {
        margin-right: 16px;
        opacity: 0.4;
      }
      &-copy {
        margin-left: 7px;
        opacity: 0.4;

        &:hover {
          opacity: 1;
        }
      }
    }
    &:hover, &:hover &__cell {
      background: #F3F5F9;
    }
    &:hover &__cell {
      border-bottom: 1px solid rgba(0, 0, 0, 0);

      &:before {
        display: none;
      }
    }
  }
}
</style>
